
import React, { useState } from 'react';
import SideNav, { DocItem } from './sidebar-nav';
import { Section, SectionType } from '../layout';
import { Container, Row, Col } from 'reactstrap';
import VisibilitySensor from 'react-visibility-sensor';
import { graphql, useStaticQuery } from 'gatsby';
import { QueryResult, SingularResult } from 'ProjectDescriptionExcerpt';
import ReactMarkdown from 'react-markdown';
import OutcomeMedia from './outcome-media';
const whereWeWork = require('./where-we-work.jpg');

const minTopValue = 150;

export default () => {

    const [inView, setInView] = useState(DocItem.background);

    const data = useStaticQuery(graphql`
    query ProjectDocumentation {
        strapiProjectDescription {
            background
            achievements
            next
            objectives
            outcomes {
                cover {
                    publicURL
                }
                body
                title
            }
        }
    }
    `) as SingularResult;

    return (
        <Section type={SectionType.darkOverWhite}>
            <Container>
                <Row>
                    <Col md="3">
                        <SideNav active={inView} />
                    </Col>
                    <Col md="9">
                        {/* {[1, 2, 3, 4].map(idx => (
                            <VisibilitySensor
                                onChange={(v) => console.log(`#${idx} ${v ? 'visible' : 'invisible'}`)}
                                partialVisibility={true}
                                minTopValue={200}
                                key={idx}
                            >
                                <ReactMarkdown source={data.strapiProjectDescription.background} />
                            </VisibilitySensor>
                        ))} */}
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.background)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="background" className="mb-4 text-primary">Background</h2>
                                <ReactMarkdown source={data.strapiProjectDescription.background} />
                            </>
                        </VisibilitySensor>
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.objectives)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="objectives" className="mt-5 mb-4 text-primary">Objectives</h2>
                                <ReactMarkdown source={data.strapiProjectDescription.objectives} />
                            </>
                        </VisibilitySensor>
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.results)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="results" className="mt-5 mb-4 text-primary">Expected Results</h2>
                                <ol>
                                    {data.strapiProjectDescription.outcomes.map((outcome, idx) => (
                                        <OutcomeMedia outcome={outcome} key={idx} />
                                    ))}
                                </ol>
                            </>
                        </VisibilitySensor>
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.locations)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="locations" className="mt-5 mb-4 text-primary">Where we work?</h2>
                                <img
                                    src={whereWeWork}
                                    className="img-fluid mx-auto d-block"
                                />
                            </>
                        </VisibilitySensor>
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.achievements)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="achievements" className="mt-5 mb-4 text-primary">Achievements - 2019</h2>
                                <ReactMarkdown source={data.strapiProjectDescription.achievements} />
                            </>
                        </VisibilitySensor>
                        <VisibilitySensor
                            onChange={(v) => v && setInView(DocItem.next)}
                            partialVisibility={true}
                            minTopValue={minTopValue}
                        >
                            <>
                                <h2 id="next" className="mt-5 mb-4 text-primary">What's next?</h2>
                                <ReactMarkdown source={data.strapiProjectDescription.next} />
                            </>
                        </VisibilitySensor>
                    </Col>
                </Row>
            </Container>
        </Section>
    );
}