import React from 'react';
import { NavItem, NavLink, Nav } from 'reactstrap';
import { AnchorLink } from "gatsby-plugin-anchor-links";


export enum DocItem {
    background,
    objectives,
    results,
    locations,
    achievements,
    next
}

type props = {
    active: DocItem
}

export default ({ active }: props) => {
    return (
        <Nav vertical={true} className="sub-nav sticky-top">
            <NavItem active={active == DocItem.background}>
                <AnchorLink
                    to="/project-desc#background"
                    title="Background"
                    className="nav-link"
                />
            </NavItem>
            <NavItem active={active == DocItem.objectives}>
                <AnchorLink
                    to="/project-desc#objectives"
                    className="nav-link"
                    title="Objectives"
                />
            </NavItem>
            <NavItem active={active == DocItem.results}>
                <AnchorLink
                    to="/project-desc#results"
                    className="nav-link"
                    title="Expected Results"
                />
            </NavItem>
            <NavItem active={active == DocItem.locations}>
                <AnchorLink
                    to="/project-desc#locations"
                    className="nav-link"
                    title="Where we work?"
                />
            </NavItem>
            <NavItem active={active == DocItem.achievements}>
                <AnchorLink
                    to="/project-desc#achievements"
                    className="nav-link"
                    title="Achievements - 2019"
                />
            </NavItem>
            <NavItem active={active == DocItem.next}>
                <AnchorLink
                    to="/project-desc#next"
                    className="nav-link"
                    title="What's next?"
                />
            </NavItem>
        </Nav>
    );
}